body{
    background-color:white;
    margin: 0%;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.text_row1{
    background-color:white;
    display: flex;
    flex-direction: row;
    position: fixed;
    width: 100%;
    margin-top: 0%;

}
.text_row1_left{
    background-color:white;
    width: 45%;
    display: flex;
    flex-direction: row;
    padding-left: 5%;
    padding-top: 10px;
    padding-bottom: 10px;
}
.text_row1_right{
    background-color: white;
    width: 45%;
    display: flex;
    flex-direction: row;
    justify-content: right;
    align-items: center;
    padding-right: 5%;
}
.text_row1_left_text{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
}
.text_row1_left_text_title{
    font-size: 1.4rem;
}
.text_row1_right button{
    background-color: rgb(21, 88, 140);
    font-size: medium;
    color: white;
    margin-left: 10px;
    padding: 10px 20px;
    border: none;
    border-radius: 20px;
}
.text_label_1{
    color: blue;
}
.text_label_3{
    color:blue;

}

.text_row1_right button:hover{
    background-color: rgb(6, 36, 54);
}
.text_row3{
    background-color: yellow;
}
.text_row3 ul{
    background-color: white;
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 20px 5%;
    justify-content: space-between;
}
.text_row3 li{
    background-color: white;
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 18%;
}
.text_row2{
    background-color: gray;
    padding: 100px 20%;
    background-attachment: fixed;
}
.text_row2_inner{
    background-color:rgba(0, 0, 0, 0.425);
    padding: 20px 5%;
    display: flex;
    flex-direction: column;
}
.text_row2_inner_row3{
    background-color: white;
    display:flex;
    flex-direction: row;
    border-radius: 10px;
}
.text_row2_inner_row3_col1,
.text_row2_inner_row3_col2,
.text_row2_inner_row3_col3{
    width: 25%;
    padding-top: 30px;
    padding-bottom: 10px;

}

.text_row2_inner_row3 button{
    font-size: medium;
    width: 25%;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    border: none;
    color: white;
    background-color:rgb(21, 88, 140);
}
.text_row2_inner_row3 button:hover{
    background-color: rgb(6, 36, 54);
}
.text_row2_inner_row3 input,
.text_row2_inner_row3 select{
    width: 100%;
    box-sizing: border-box;
    border: none;
    outline: none;
    padding-top: 5px;
    padding-bottom: 5px;
}
.text_row2_inner_row1{
    font-size: 1.5rem;
    color: white;
}
.text_row2_inner_row2{
    color: white;
    font-size: 1.4rem;
}
.text_row2_inner_row4{
    text-align: right;
    color: white;

}
.text_row2_inner_row3_col2{
    border-left: 1px solid gray;
    border-right: 1px solid gray;
}
.text_row2_inner_row3_col1{
    padding-left: 10px;
}
